import React from "react";
import { Dayjs } from "dayjs";
import { mergeClassName } from "../../../common";
import { getCellDateDisabled, PanelMode } from "../date-util";
import { CalendarTable, Td, Cell, TableWrapper } from "./styled";

interface IPanelBodyProps {
  headerCells?: React.ReactNode;
  rowNum: number;
  colNum: number;
  mode: PanelMode;

  viewDate: Dayjs;
  baseDate: Dayjs;
  disabledDate?: (date: Dayjs) => boolean;
  onSelect: (value: Dayjs) => void;
  getCellDate: (date: Dayjs, offset: number) => Dayjs;
  getCellText: (date: Dayjs) => React.ReactNode;
  getCellClassName: (date: Dayjs) => (string | undefined)[];
}

export const PanelBody: React.FC<IPanelBodyProps> = React.memo((props) => {
  const {
    headerCells,
    rowNum,
    colNum,
    baseDate,
    viewDate,
    mode,
    getCellDate,
    getCellText,
    onSelect,
    disabledDate,
    getCellClassName,
  } = props;

  const rows: React.ReactNode[] = [];

  for (let i = 0; i < rowNum; i++) {
    const row: React.ReactNode[] = [];

    for (let j = 0; j < colNum; j++) {
      const offset = i * colNum + j;
      const currentDate = getCellDate(baseDate, offset);
      const disabled = getCellDateDisabled({
        cellDate: currentDate,
        mode,
        disabledDate,
      });
      const curDate = getCellText(currentDate);

      row.push(
        <Td key={j} onClick={() => !disabled && onSelect(currentDate)}>
          <Cell
            className={mergeClassName(
              disabled ? "disabled" : undefined,
              ...getCellClassName(currentDate)
            )}
          >
            {curDate}
          </Cell>
        </Td>
      );
    }

    rows.push(<tr key={i}>{row}</tr>);
  }

  return (
    <TableWrapper>
      <CalendarTable>
        {headerCells && (
          <thead>
            <tr>{headerCells}</tr>
          </thead>
        )}
        <tbody>{rows}</tbody>
      </CalendarTable>
    </TableWrapper>
  );
});
